@charset "UTF-8";

* {
  margin: 0;
  padding: 0;
  color: #666666;
}

ul {
  list-style: none;
}
a:link,
a:visited {
  text-decoration: none;
}
header {
  width: 1340px;
  height: 100px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
header h2 {
  width: 200px;
}
header h2 img {
  width: 100%;
}
header ul {
  width: 600px;
  display: flex;
}
header li {
  width: 150px;
  text-align: center;
  height: 40px;
  background-color: rgb(145, 201, 201);
  line-height: 40px;
  position: relative;
}
header div {
  background-color: white;
  width: 150px;
  height: 0;
  overflow: hidden;
  transition: all 0.4s;
  position: absolute;
  z-index: 10;
}
header li:hover div {
  height: 160px;
}
header li a {
  display: block;
  width: 150px;
}
header li a:hover {
  background-color: lightgreen;
}
header li:hover {
  background-color: lightgreen;
}
figure {
  width: 100%;
  height: 550px;
  position: relative;
}
figure a {
  position: absolute;
  left: 0;
  top: 0;
}
main {
  width: 1340px;
  height: 440px;
  margin: auto;
  position: relative;
}
main article:nth-child(1) {
  position: absolute;
  z-index: 2;
  top: -80px;
  left: 0;
  background-color: white;
  width: 100%;
}
main article:nth-child(1) ul {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 80px;
}
main article:nth-child(1) li {
  width: 80px;
  height: 80px;
}
main article:nth-child(1) img {
  width: 100%;
  height: 100%;
}

main article:nth-child(2) {
  width: 100%;
  height: 200px;
  background: url(../images/banner.jpg) center/cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

main article:nth-child(2) h2 {
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  background-color: hsla(0, 0%, 100%, 0.5);
  text-align: center;
  line-height: 180px;
}

article .tab {
  width: 320px;
  height: 40px;
  line-height: 40px;
  display: flex;
  margin-top: 8px;
}
.tab h3 {
  width: 50%;
  text-align: center;
  cursor: pointer;
}
.tab h3:nth-child(1) {
  background-color: darkcyan;
  color: white;
}
.tab h3:nth-child(2) {
  background-color: darkgoldenrod;
  color: white;
}

article .notice {
  display: block;
}
.notice ul {
  width: 600px;
}
.notice li {
  width: 100%;
  line-height: 40px;
  display: flex;
}
.notice p:nth-child(1) {
  width: 80%;
}
.notice p:nth-child(2) {
  width: 20%;
}
.notice li:nth-child(2n) {
  background-color: darkgray;
}

article .gallery {
  display: none;
  margin-top: 40px;
}
.gallery ul {
  width: 400px;
  display: flex;
}
.gallery li {
  width: 30%;
  margin-right: 1%;
}

footer {
  width: 1340px;
  height: 120px;
  background-color: lightpink;
  margin: auto;
}

.popup {
  width: 600px;
  height: 400px;
  background-color: white;
  position: fixed;
  z-index: 11;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid;
  line-height: 2em;
  padding: 2em;
  display: block;
}
.popup .close {
  width: 80px;
  height: 40px;
  position: absolute;
  bottom: 0;
  right: 0;
}

figure .swiper{
  width: 100%;
  height: 550px;
}

.swiper img{
  width: 150% !important; 
}

figure .swiper-slide{
  width: 100%;
}